"use client";
import { useState } from "react";
import { auth, db } from "../../firebase";
import { signInWithEmailAndPassword, createUserWithEmailAndPassword, updateProfile } from "firebase/auth";
import { doc, setDoc, getDocs, collection, query, where } from "firebase/firestore";

export default function Login() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [name, setName] = useState("");
  const [nametag, setNametag] = useState("");
  const [isRegister, setIsRegister] = useState(false);

  const handleLogin = async () => {
    try {
      await signInWithEmailAndPassword(auth, email, password);
      window.location.href = "/chat";
    } catch (e) {
      alert(e.message);
    }
  };

  const generateUniqueNametag = async (base) => {
    const usersRef = collection(db, "users");
    let tag = base;
    let counter = 1;
    while (true) {
      const q = query(usersRef, where("nametag", "==", tag));
      const snapshot = await getDocs(q);
      if (snapshot.empty) break;
      tag = `${base}#${counter}`;
      counter++;
    }
    return tag;
  };

  const handleRegister = async () => {
    if (!nametag) {
      alert("Digite uma nametag");
      return;
    }
    try {
      const userCredential = await createUserWithEmailAndPassword(auth, email, password);
      const finalNametag = await generateUniqueNametag(nametag);
      await updateProfile(userCredential.user, { displayName: name, photoURL: "" });
      await setDoc(doc(db, "users", userCredential.user.uid), {
        uid: userCredential.user.uid,
        name,
        email,
        photoURL: "",
        nametag: finalNametag
      });
      window.location.href = "/chat";
    } catch (e) {
      alert(e.message);
    }
  };

  return (
    <div style={{ display:"flex", justifyContent:"center", alignItems:"center", height:"100vh", flexDirection:"column", background:"#f2f2f2" }}>
      <h2>{isRegister ? "Cadastro" : "Login"}</h2>
      {isRegister && <>
        <input placeholder="Nome" value={name} onChange={e=>setName(e.target.value)} style={inputStyle}/>
        <input placeholder="Nametag" value={nametag} onChange={e=>setNametag(e.target.value)} style={inputStyle}/>
      </>}
      <input placeholder="Email" value={email} onChange={e=>setEmail(e.target.value)} style={inputStyle}/>
      <input placeholder="Senha" type="password" value={password} onChange={e=>setPassword(e.target.value)} style={inputStyle}/>
      <button onClick={isRegister ? handleRegister : handleLogin} style={buttonStyle}>
        {isRegister ? "Registrar" : "Entrar"}
      </button>
      <p style={{ cursor:"pointer", color:"blue" }} onClick={()=>setIsRegister(!isRegister)}>
        {isRegister ? "Já tem conta? Login" : "Não tem conta? Registrar"}
      </p>
    </div>
  );
}

const inputStyle = { margin:"5px", padding:"8px", borderRadius:"8px", border:"1px solid #ccc", width:"250px" };
const buttonStyle = { padding:"8px 12px", margin:"5px", borderRadius:"8px", border:"none", background:"#4caf50", color:"#fff", cursor:"pointer" };
